.item {
  display: flex;
  flex-direction: column;
  gap: 24px;
  background: #1C1C21;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  box-sizing: border-box;
}

.item:hover {
  cursor: pointer;
}

.details {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.content {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  gap: 24px;
}

.ingredients {
  margin-right: auto;
  width: 304px;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(6, 48px);
}

.price {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 208px;
}
